<template>
  <main class="mt-3">
      <div class="mb-1">
        <h3 style="text-align:center;">결제하기</h3>
      </div>

      <!-- location -->
      <div class="container">
        <div class="row box_border">
          <div class="col-8">
            <h5>매장</h5>
            <h5>th신논현역점</h5>
u
          <div class="col-4">
            <p>서울 강남구 강남대로 476 어반하이브</p>
          </div>
        </div>

        <div class="row" :key="i" v-for="(product, i) in productList">
          <h5>주문내역</h5>
          <div class="">
            <div>
              <img v-if="product.path!=null" :src="`/download/${product.id}/${product.path}`" style="height:50px;width:auto;" />
            </div>
            <div> 
                <div>
                  <span>제품명</span>
                  <span>{{ product.product_name }}</span>
                </div>
                <div>
                  <span>온도</span>
                  <span class="badge bg-dark me-1">{{ product.temp_description }}</span>
                </div> 
                <div>
                  <span>컵선택</span>
                  <span class="badge bg-dark me-3">{{ product.cup_description }}</span>
                </div>
                <div>
                  <span>픽업방법</span>
                  <span class="badge bg-dark me-3">{{ product.pickup }}</span>
                </div>                          
                <div>
                  <span>총합계</span>
                  <span>{{ product.order_price }}</span>
                </div> 
            </div>
          </div>
        </div>
      </div>
      </div>
      <table class="table table-bordered">
        <thead>
          <tr>
            <th></th>
            <th>제품명</th>
            <th>온도</th>
            <th>컵선택</th>
            <th>픽업</th>
            <th>총합계</th>
            <th>주문취소</th>
            <th>관리자 모드</th>
          </tr>
        </thead>
        <tbody>
          <!-- for문 -->
          <tr :key="i" v-for="(product, i) in productList">
            <td>
              <img v-if="product.path!=null" :src="`/download/${product.id}/${product.path}`" style="height:50px;width:auto;" />
            </td>
            <td>{{ product.product_name }}</td>
            <td>{{ product.temp_description }}</td>
            <td>{{ product.cup_description }}</td>
            <td>{{ product.pickup_description }}</td>
            <td>{{ product.order_price }}</td>
            <td>
               <button type="button" class="btn btn-danger" @click="deleteProduct(product.id);">삭제</button>
            </td>
            <td>
              <button type="button" class="btn btn-info me-1" @click="goToImageInsert(product.id);">사진등록</button>
              <button type="button" class="btn btn-warning me-1" @click="goToUpdate(product.id);">수정</button>
            </td>
          </tr>
        </tbody>
      </table>   
  </main>
</template>

<script>
// eslint-disable-next-line
/* eslint-disable */

export default {
  data() {
    return {
      productList: []
    };
  },
  created() {
    this.getProductList();
  },
  methods: {
    async getProductList() {
      this.productList = await this.$api("/api/productList2",{});
      console.log(this.productList);
    },
    goToInsert() {
      this.$router.push({path:'/create'}); //페이지 router이동
    },
    goToDetail(product_id) {
     this.$router.push({path:'/detail', query:{product_id:product_id}}); 
    },
    goToUpdate(product_id) {
      this.$router.push({path:'/Update', query:{product_id:product_id}}); 
    },
    goToImageInsert(product_id) {
      this.$router.push({path:'/image_insert', query:{product_id:product_id}}); 
    },
    deleteProduct(product_id) {
      this.$swal.fire({
        title: '정말 삭제하시겠습니까?',
        showCancelButton: true,
        confirmButtonText: `삭제`,
        cancelButtonText: `취소`
      }).then(async (result) => {
        if (result.isConfirmed) {
          console.log(product_id)
          await this.$api("/api/productDelete",{param:[product_id]});
          this.getProductList();
          this.$swal.fire('삭제되었습니다!', '', 'success')
        } 
      });
    }
  }
}

</script>

<style scoped>
body {
  background:#ececec;
}
.box_border{
  width: 60%;
  background-color:white;
  border-radius:15px;
}
</style>