<template>
<!--  eslint-disable-next-line -->
<!--  eslint-disable -->
 <main class="mt-3">
    <div class="container">
      <button type="button" class="btn btn-dark">
        <router-link class="navbar-brand" to="/management">제품관리</router-link>
      </button>
      <!-- <h2 class="text-center">제품 등록</h2> -->
      <div class="mb-3 row">
        <label class="col-md-3 col-form-label">제품명</label>
        <div class="col-md-9"> <!-- col-md-9 입력단 길이 -->
          <input type="text" class="form-control" v-model="product.product_name">
        </div>
      </div>

      <div class="mb-3 row" v-for="(priceItem, index) in prices" :key="index">
        <label class="col-md-3 col-form-label">제품가격 ({{ index + 1 }})</label>
        <div class="col-md-9"> 
          <div class="input-group mb-3">
            <input type="number" class="form-control" v-model="priceItem.value">
            <span class="input-group-text">원</span>
          </div>
        </div>
      </div>

      <div class="mb-3 row">
        <label class="col-md-3 col-form-label">제품 설명</label>
        <div class="col-md-9"> <!-- col-md-9 입력단 길이 -->
          <input type="text" class="form-control" v-model="product.product_subtitle">
        </div>
      </div>
      
      <!-- 제품 카테고리 -->
      <div class="mb-3 row">
        <label class="col-md-3 col-form-label">제품카테고리</label>
        <div class="col-md-9">
           <div class="row">
            <div class="col-auto">
              <select class="form-select">
                <option>카테고리</option>
              </select>
           </div>
           
            <div class="col-auto">
              <select class="form-select">
                <option>카테고리</option>
              </select>
           </div>

            <div class="col-auto">
              <select class="form-select">
                <option>카테고리</option>
              </select>
           </div>
          </div>
        </div>
      </div>

      <!-- 태그 -->
      <div class="mb-3 row">
        <label class="col-md-3 col-form-label">태그</label>
        <div class="col-md-9">
          <input type="text" class="form-control" v-model="product.tags">
        </div>
      </div>
    
    <!-- 버튼 -->
    <div class="mb-3 row">
        <div class="col-6 d-grid p-1">
            <button type="button" class="btn btn-dark" @click="goToList">취소하기</button>
        </div>
        <div class="col-6 d-grid p-1">
            <button type="button" class="btn btn-danger" @click="productInsert">저장하기</button>
        </div>
    </div>
    </div>
  </main>
</template>

<script>
// eslint-disable-next-line
/* eslint-disable */

export default {
  data() {
    return {
      product: { // v-model
        product_name: "",
        product_subtitle: "",
        tags: "",
        category_id: 1,
        seller_id: 1,
      },
      prices: [
        { type: 1, value: 0 }, // small
        { type: 2, value: 0 }, // medium
        { type: 3, value: 0 }  // large
      ]
    };
  },
  computed: {
    user(){
      return this.$store.state.user;
    }
  },
  // mounted(){ //로그인 해야 이용하는 기능
  //   if(this.user.email == undefined){
  //     alert("로그인을 해야 이용할 수 있습니다");
  //     this.$router.push({path:'/'});
  //   }
  // },
  methods: {
    goToList() {
        this.$router.push({path:'/management'});
    },
    productInsert(){    
    if (this.product.product_name == ""){
      return this.$swal("제품명은 필수 입력값입니다");
    }
    if (this.product.product_price == ""){
      return this.$swal("가격은 필수 입력값입니다");
    }
    if (this.product.delivery_price == "" || this.product.delivery_price == 0){
      return this.$swal("배송료를 입력하세요");
    }
    if (this.product.outbound_days == "" || this.product.outbound_days == 0){
      return this.$swal("출고일을 입력하세요");
    }
    this.$swal.fire({
      title: '정말 등록 하시겠습니까?',
      showCancelButton: true,
      confirmButtonText: '생성',
      cancelButtonText: '취소',
      }).then(async (result) => {
      if(result.isConfirmed){
        await this.$api('/api/productInsert', {
          param:[
            this.product.product_name,
            this.product.product_subtitle,
            this.product.tags,
            this.product.seller_id,
            this.product.category_id,
            this.prices[0].value, // small price
            this.prices[1].value, // medium price
            this.prices[2].value, // large price
          ],       
      }); //product 배열
        console.log(this.product);
        this.$swal.fire('저장되었습니다!', '', 'success');
        this.$router.push({path:'/management'});                             
        }
    });
  },


  }
};
</script>